.outer-grid {
  display: grid;
  grid-template-rows: 18vh 2.5vh 18vh 18vh 18vh 2.5vh 18vh;
  grid-template-columns: auto 2.5vw 18vw 18vw 18vw 2.5vw auto;

  height: 100%;
  width: 100%;
}

/* bunlar başka türlü addresslenir mi? */
.custom-operation {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 4;
  grid-column-end: 5;
  width: min-content;
}
.custom-operation2 {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 7;
  grid-column-end: 8;
  width: min-content;
}
.custom-operation3 {
  grid-row-start: 7;
  grid-row-end: 8;
  grid-column-start: 4;
  grid-column-end: 5;
  width: min-content;
}
.custom-operation4 {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 1;
  grid-column-end: 2;
  width: min-content;
  justify-self: right;
}

.custom-operation > div,
.custom-operation2 > div,
.custom-operation3 > div,
.custom-operation4 > div {
  background: darkcyan;
}

/* why this doesn't work */
.linarrow1,
.linarrow2,
.linarrow3,
.linarrow4 {
  height: 2rem;
}

.linarrow-image-container:nth-of-type(5) {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 4;
  grid-column-end: 5;
}

.linarrow-image-container:nth-of-type(6) {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 6;
  grid-column-end: 7;
}

.linarrow-image-container:nth-of-type(7) {
  grid-row-start: 6;
  grid-row-end: 7;
  grid-column-start: 4;
  grid-column-end: 5;
}

.linarrow-image-container:nth-of-type(8) {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 2;
  grid-column-end: 3;
}

.discourse-grid {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row-start: 3;
  grid-row-end: 6;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;

  height: 100%;
  width: 100%;
}

.discourse-container {
  width: 18vw;
  height: 18vh;

  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  background: red;
  color: white;
  justify-self: center;
}

.discourse-container:nth-of-type(2) {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
  background: blue;
  justify-self: left;
}

.discourse-container:nth-of-type(3) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  background: green;
}

.discourse-container:nth-of-type(4) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  background: purple;
  justify-self: right;
}

.discourse-container > div {
  height: 10%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.discourse-container .discourse-selector,
.discourse-container .discourse-display {
  height: 90%;
}

.image-container {
  justify-self: center;
  align-self: center;
}

.stalin-image-container {
  height: 18vh;
  width: 18vh;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}

.stalin {
  border-radius: 50%;
  max-height: 100%;
  max-width: 100%;
}

.cwarrow-image-container {
  /* height: 14rem; */
  height:18vh;
  display: flex;
  justify-self: unset;
}

.cwarrow-image-container:nth-of-type(6) {
  padding-bottom: 2rem;
  padding-right: 4rem;
  padding-top: 4rem;
  justify-self: right;
  align-self: flex-end;
}
.cwarrow-image-container:nth-of-type(7) {
  padding-bottom: 2rem;
  padding-left: 4rem;
  padding-top: 4rem;
  justify-self: left;
  align-self: flex-end;
}

.cwarrow-image-container:nth-of-type(8) {
  padding-bottom: 4rem;
  padding-right: 4rem;
  padding-top: 2rem;
  justify-self: right;
  align-self: flex-start;
}
.cwarrow-image-container:nth-of-type(9) {
  padding-bottom: 4rem;
  padding-left: 4rem;
  padding-top: 2rem;
  justify-self: left;
  align-self: flex-start;
}
.cwarrow2,
.linarrow2 {
  transform: rotate(90deg);
}
.cwarrow3,
.linarrow3 {
  transform: rotate(180deg);
}
.cwarrow4,
.linarrow4 {
  transform: rotate(270deg);
}
