.discourse-selector {
  background: lightgray;

  display: grid;
  grid-template-columns: .3fr .6fr 1fr .6fr .3fr;
  grid-template-rows: 3fr 0.1fr 3fr;

  width: 100%;
  padding: 0.5rem;
}

.matheme-selector {
  background: transparent;
}

.matheme-selector  {
  position:relative;
  border:1px solid black;
}
.matheme-selector:before {
  content:'';
  position:absolute;
  top:-1px; left:-1px;
  border:1px solid coral;
  height:100%;
  width:100%;
  border-radius: 10%;
  background: red;
}